:root {
  --background-color: rgba(255,255,223,255);
  --box-color: rgba(215,222,241,255);
  --border-color: rgba(173,193,242,255);
}

.App{
  background-color: var(--background-color);
}

.value-block {
  background-color: aquamarine;
  padding: 20px;
  display: flex;
  flex-direction: column;
}

.value-block input {

}

.value-block .label {

}

.main-block {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 0 1em;
  gap: 1em;
  align-self: stretch;
}



.params-block {
  display: flex;
  flex-direction: column;
  width: fit-content;
  gap: 0.5em;
}
.params-block .element {
  padding: 0.5em;
  background-color: var(--box-color);
  border-radius: 0.5em;
  align-items: center;

  border: 3px solid var(--border-color);
}
.params-block .element .label {
  text-align: center;
}
.params-block .element input {

}


.main-block .list {
  display: flex;
  flex-direction: row;
  gap: 1em;
  width: 100%;
  overflow-x: auto;
  padding: 0.5em;
  background-color: var(--box-color);
  border-radius: 0.5em;
  align-self: stretch;
  flex-grow: 1;

  border: 4px solid var(--border-color);
}
.main-block .list::-webkit-scrollbar {
  width: 10px;
}
.main-block .list::-webkit-scrollbar-track {
  background-color: lightblue;
  border-radius: 10px;
}
.main-block .list::-webkit-scrollbar-thumb {
  background-color: rgb(150, 150, 200);
  border-radius: 10px;
}
.main-block .list-container {
  
}
.main-block .control {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-self: stretch;
  padding: 1em 0;
  gap: 0.55em
}
.main-block .control button {
  background-color: var(--box-color);
  border-width: 1;
  border-color: var(--border-color);
  border-radius: 10px;
  border-style: double;

  transition: background-color 0.15s;
}
.main-block .control button:hover {
  background-color: rgb(201, 218, 243);
}

.main-block .control button {
  padding: 0.2em;
}

.main-block .list .element {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 0.5em;
  background-color: rgb(150, 150, 200);
  padding: 0.5em;
  border: solid;
  border-color: transparent;
  transition: all 0.05s;
}

.main-block .list .element.element.selected {
  background-color: rgb(130, 130, 220);
}

.main-block .list .element:hover,
.main-block .list .element.selected:hover {
  border-color: rgb(100, 100, 150);
}

.main-block .list .element .label {

}

.main-block .list .element .img {
  height: 100%;
}

.full-view-container {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  background-color: rgba(0, 0, 0, 0.4);
  display: flex;
}

.full-view {
  background-color: var(--box-color);
  width: 70%;
  transform: translate(15%, 15%);
  height: fit-content;


  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1em 0;
  gap: 0.5em;

  border-radius: 1em;
}
.full-view .header {
  font-size: 2em;
}

.full-view .image {

}

.full-view .info {
  display: flex;
  flex-direction: column;
  align-self: stretch;
  align-items: center;
  padding: 0 7.5%;
}

.full-view .info .lines {
  display: flex;
  justify-content: space-around;
  align-self: stretch;
}

.full-view .info .text {

}

.hide {
  display: none;
}